<template>
  <div class="w-full">
    <Navbar @display-mobile-menu="display" />
    <MenuMobile
      class="lg:hidden"
      @hide-menu="hideMenu"
      :display-mobile-menu="displayMenu"
    />
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import MenuMobile from "@/components/MenuMobile.vue";

export default {
  name: "Navigation",
  components: {
    Navbar,
    MenuMobile,
  },
  data() {
    return {
      displayMenu: false,
    };
  },
  methods: {
    display() {
      this.displayMenu = true;
    },
    hideMenu() {
      this.displayMenu = false;
    },
  },
};
</script>
