import { createApp } from "vue";
import App from "./App.vue";
import "./assets/tailwind.css";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { createRouter, createWebHistory } from "vue-router";
import VueSplide from "@splidejs/vue-splide";

import Home from "./pages/Home.vue";
import AboutMe from "./pages/AboutMe.vue";
import LoveStory from "./pages/LoveStory.vue";
import FamilyStory from "./pages/FamilyStory.vue";
import Baptism from "./pages/Baptism.vue";
import ContactForm from "./pages/ContactForm.vue";

const routes = [
  { path: "/", component: Home },
  { path: "/about-me", component: AboutMe },
  { path: "/love-story", component: LoveStory },
  { path: "/family-story", component: FamilyStory },
  { path: "/baptism", component: Baptism },
  { path: "/contact-form", component: ContactForm },
];

const router = createRouter({
  history: createWebHistory(),
  routes: routes,
});

createApp(App)
  .component("font-awesome-icon", FontAwesomeIcon)
  .use(router, VueSplide)
  .mount("#app");
