<template>
  <div>
    <img
      :src="selectedImage.imgSrc"
      style="aspect-ratio: 1/1"
      class="object-cover mb-10 bell-shape"
    />
    <div class="flex flex-row justify-center space-x-6 text-center mb-20">
      <button @click="selectedImageId -= 1">
        <font-awesome-icon class="text-brown-700 text-xl" icon="arrow-left" />
      </button>

      <button @click="selectedImageId += 1">
        <font-awesome-icon class="text-brown-700 text-xl" icon="arrow-right" />
      </button>
    </div>
  </div>
</template>
<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faArrowLeft, faArrowRight } from "@fortawesome/free-solid-svg-icons";
library.add(faArrowLeft, faArrowRight);

export default {
  name: "ImageCarousel",
  props: {
    images: {
      require: true,
      type: Array,
    },
  },
  data() {
    return {
      selectedImageId: this.images[0].id,
      
    };
  },
  computed: {
    selectedImage() {
      return this.images.find((item) => item.id === this.selectedImageId);
    },
  },
};
</script>

<style scoped>
@media (min-width: 1024px) {
  .bell-shape {
    clip-path: path("M 0 200 L 0,100 A 5,5 0,0,1 200,100 L 200 200 z");
  }
}
</style>
