<template>
  <div>
    <h2>Ostatnie posty</h2>
    <img :src="lastPostElement.imgSrc" />
    <h3>{{ lastPostElement.title }}</h3>
    <p>{{ lastPostElement.text }}</p>
    <Button>Zobacz więcej</Button>
  </div>
</template>

<script>
import Button from "@/components/Button.vue";

export default {
  name: "LastPostSummary",
  components: {
    Button,
  },
  date() {
    return {
      activePost: this.lastPostElement[0].id,
    };
  },
  computed: {
    selectedImage() {
      return this.lastPostElement.find(
        (item) => item.id === this.lastPostElement
      );
    },
  },
  props: {
    lastPostElement: {
      type: Array,
      require: true,
    },
  },
};
</script>
